// SideBar.tsx

import React, { FC, useState, useEffect } from 'react';
import './Navigator.css';

interface SideBarProps {
  onLinkClicked: (selectIdx: number) => void;
  onReturnClick: () => void;
  onCurLessonClick: () => void;
  isOpen: boolean;
  isReturn: boolean;
  dispList?: SidebarItem[];
  LessonText?: string;
}

interface SidebarItem {
  label: string;
  key: number;
  nestedList?: SidebarItem[];
}

const SideBar: FC<SideBarProps> = ({ onLinkClicked, onReturnClick, onCurLessonClick, isOpen, isReturn, dispList, LessonText }) => {
  
  // リンクがクリックされた時の処理
  const handleItemClick = (selectIdx: number) => {
    onLinkClicked(selectIdx);
  };

  // 授業名がクリックされた時の処理
  const handleCurLessonClick =() => {
    onCurLessonClick();
  };

  // 戻るがクリックされた時の処理
  const handleReturnClick = () => {
    onReturnClick();
  };
  
  
  return (
    <div className={`sidebar ${isOpen ? 'open' : ''}`}>
      {/* 戻るボタン */}
      { isReturn && (
        <button className="button-return" onClick={() => handleReturnClick()}>{`授業一覧へ`}</button>
      )}
      {/* 授業名 */}
      {( isReturn ) && ( <button className="Lesson-button" onClick={() => handleCurLessonClick()}>{LessonText}</button> )}
      <div className="sidebar-list">
        <ul>
          {dispList ? (
              dispList.map((item, index) => (
                <li key={item.label}>
                  <button onClick={() => handleItemClick(index)}>{item.label}</button>
                </li>
              ))
            ) : (
              <li>No items to display</li>
            )}
        </ul>
      </div>
    </div>
  );
};

export default SideBar;